.nav-button {
    border-bottom: none;
    display: inline-block;
    margin: 1rem 3rem 1rem 0;
    font-weight: 300;
    font-size: 18px;
    float: right;
    cursor: pointer;
}

.nav-button:hover {
    font-weight: 500;
}

header {
    background-color: $lightpink;
    width: 100%;
    height: 75vh;
    display: grid;
    align-content: center;
}

header > * {
    padding-left: 5rem;
}

.intro {
    font-family: $sans-title;
    font-weight: 300;
    font-size: 20px;
    line-height: 150%;
}

section {
    // max-width: $column-width;
    margin: 0 auto;
}

#content-grid {
    display: flex;
    flex-wrap: wrap;
    padding: 1rem 2rem;
    justify-content: center;
}

#content-grid div { 
    height: 20rem;
    width: 30%;
    background-color: $white;
    margin: 2px;
    cursor: pointer;
    background-position: center;
    background-size: cover;
}

#candidate-in-hiphop {
    background-image: url('/images/candidate-hiphop-cover.png');
}

#kids-words-dev {
    background-image: url('/images/kids-words-dev.png')
}

// ipad
@media only screen and (max-width: 900px) {

    header > * {
        padding-left: 1.5rem;
    }

    #content-grid div {
        width: 45%;
    }
  }

// mobile
@media only screen and (max-width: 550px) {

    header > * {
        padding-left: 1rem;
    }

    #content-grid div {
        width: 90%;
    }
  }